body {
    background-color: #222831;
  }
  
  .bg {
    background-color: white !important;
  }
  
  .height {
    height: 200px;
  }
  .mT-20 {
     margin-top: 20px;
  }
  
  .cont {
    display: flex;
    align-items: center;
  }
  
  .exchangeInput {
    position: absolute;
    bottom: -25px;
    padding: 6px;
    border: solid 1px green;
    border-radius: 10px;
    min-width: 120px;
    height: 35px;
    background-color: #fff;
    align-items: center;
    justify-content: center;
  }
  
  
  .form__field {
    font-family: inherit;
    width: 50px;
    border: 0;
    border-bottom: 2px solid #9b9b9b;
    outline: 0;
    font-size: 2rem;
    color: #000;
    padding: 7px;
    background: transparent;
    transition: border-color 0.2s;
  }
  .form__field::placeholder {
    color: transparent;
  }
  .form__field:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
  
  .form__field:focus {
    padding-bottom: 6px;
    border-width: 3px;
    border-image: linear-gradient(to right, #121414, #121312);
    border-image-slice: 1;
  }
  .form__field:focus ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #11998e;
  }

  .lowerContainer {
    background-color: #f5f8f9 !important
  }

  .pT-60 {
      padding-top: 60px;
  }


  